.guitars {
    max-width: 60rem;
    width: 90%;
    margin: 2rem auto;
    animation: guitars-appear 1s ease-out forwards;
}

.guitars ul {
    list-style: none;
    margin: 0;
    padding: 0;
}

@keyframes guitars-appear {
    from {
        opacity: 0;
        transform: translateY(3rem);
    }
    to {
        opacity: 1;
        transform: translateY(0);
    }
}